import React, { useState } from "react"
import InputField from "../../components/InputField/InputField"
import { Link } from "react-router-dom"
import MiniLoader from "../../components/Loaders/MiniLoader"
import { forgotPassword } from "../../utils/ApiRequests"
import { toast } from "react-toastify"

const Forgot = () => {
  const [passwordForm, setpasswordForm] = useState({
    email: "",
    password: ""
  })
  const [loading, setLoading] = useState(false)

  const handleChange = e => {
    const name = e.target.name
    const value = e.target.value
    const newEntry = { [name]: value }
    setpasswordForm({ ...passwordForm, ...newEntry })
  }

  const proceed = () => {
    if (passwordForm.email === "") {
      return false
    }
    return true
  }

  const handleSubmit = e => {
    e.preventDefault()
    setLoading(true)
    forgotPassword(passwordForm)
      .then(res => {
        toast.success(
          "Password request successful. Please proceed to your mail."
        )
      })
      .catch(err => {
        toast.error("Invalid email")
      })
      .finally(() => setLoading(false))
  }

  return (
    <div className="w-full min-h-screen flex justify-center items-center ">
      <form
        //   className=""
        onSubmit={handleSubmit}
        className="w-full lg:w-3/5 px-5 lg:px-10 py-3 lg:py-6 shadow-md"
      >
        <h1 className="font-medium text-xl  text-center mb-7">
          Forgot Password{" "}
        </h1>

        <InputField
          name="email"
          label="Email"
          placeholder="you@address.com"
          required={true}
          type="email"
          handleChange={handleChange}
          value={passwordForm.email}
        />

        <div className="flex ">
          {loading ? (
            <MiniLoader />
          ) : (
            <button
              type="submit"
              className={`py-2 lg:py-3.5 px-12 font-Poppins bg-lkhRed text-white rounded-3xl outline-none border-0 ${
                !proceed() ? "bg-lkhGrey cursor-not-allowed" : ""
              }`}
              disabled={!proceed()}
            >
              Forgot Password
            </button>
          )}
        </div>

        <div className="space-y-1 mt-2">
          {/* <Link to="/forgot" className="text-lg font-medium block">
            Forgot Password?
          </Link> */}
          <p className="text-lg font-medium">
            Have an account?{" "}
            <Link to="/login" className="font-normal">
              Log in
            </Link>
          </p>
        </div>
      </form>
    </div>
  )
}

export default Forgot
