import React, { useRef, useState } from "react";
import { truncateString } from "../../utils/Helpers";
import { BsThreeDotsVertical } from "react-icons/bs";
import useClickOutside from "../../hooks/useOnClickOutside";
import { toast } from "react-toastify";
import FullLoader from "../Loaders/FullLoader";
import ShareModal from "./ShareModal";
import DetailsModal from "./DetailsModal";
import docsManager from "../../utils/DocsManager";
import RenameModal from "./RenameModal";
import MoveModal from "./MoveModal";
import { useHistory } from "react-router-dom";
import { IoFolderOutline } from "react-icons/io5";

const SharedDocumentListItem = ({ m }) => {
  const [open, setOpen] = useState(false);
  const ref = useRef();
  useClickOutside(ref, () => setOpen(false));
  const [loading, setLoading] = useState(false);
  const [showDetails, setShowDetails] = useState(false);
  const history = useHistory();

  const handleOpen = () => {
    if (m.type === "board") {
      const win = window.open(`/?docid=${m.docid}`, "_blank");
      //win.focus();
    } else if (m.type === "folder") {
      docsManager.pushParent(m.docid);
      history.push(docsManager.getRoute());
      docsManager.reload();
    }
  };

  return (
    <div
      className="w-full h-40 lg:h-52 rounded-2xl shadow-md px-2 py-4 flex flex-col justify-between transition-all hover:bg-gray-100"
      ref={ref}
      onDoubleClick={() => {
        handleOpen();
      }}
    >
      {loading && <FullLoader />}
      {showDetails && (
        <DetailsModal m={m} close={() => setShowDetails(false)} />
      )}
      {m.type === "board" && (
        <img
          src={require("../../assests/icons/Logo-grey.png").default}
          alt="Board Icon"
          className="w-full h-1/2 object-scale-down"
        />
      )}

      {m.type === "folder" && (
        <IoFolderOutline className="w-full h-1/2 object-scale-down"></IoFolderOutline>
      )}

      <div className="flex items-center justify-between">
        <h1 className="m-0 text-sm">{truncateString(m.name, 12)}</h1>

        <div className="relative">
          <BsThreeDotsVertical
            onClick={() => setOpen(!open)}
            className="cursor-pointer"
          />

          {open && (
            <div className="absolute top-0 right-3 py-3 px-7 shadow-md bg-white text-left">
              <p
                className="text-sm cursor-pointer m-0"
                onClick={() => {
                  handleOpen();
                }}
              >
                Open
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default SharedDocumentListItem;
