import { AiOutlineClose } from "react-icons/ai";
import { Dialog } from "@material-ui/core";
import Select from "react-select";
import InputField from "../InputField/InputField";
import { useState, useEffect } from "react";
import { deleteShare, shareDoc } from "../../utils/ApiRequests";
import MiniLoader from "../Loaders/MiniLoader";
import { toast } from "react-toastify";
import { HiInformationCircle } from "react-icons/hi";
import useFetch from "../../hooks/useFetch";
import { IoArrowBackCircleSharp } from "react-icons/io5";
import { IoMdMail } from "react-icons/io";
import { mutate } from "swr";
import { BiArrowBack } from "react-icons/bi";
import { CopyToClipboard } from "react-copy-to-clipboard";

const ShareModal = ({ state, close, id }) => {
  const [email, setEmail] = useState("");
  const [type, setType] = useState("private");
  const [access, setAccess] = useState("read");
  const [loading, setLoading] = useState(false);
  const [viewShares, setViewShares] = useState(false);
  const { data, loading: fetch } = useFetch(id ? `/v1.1/docs/${id}/share` : "");
  const [copied, setCopied] = useState(false);
  const [error, setError] = useState(null);
  const options = [
    { value: "private", label: "Private" },
    { value: "public", label: "Public" }
  ];
  const accessOptions = [
    { value: "read", label: "Read" },
    { value: "write", label: "Write" }
  ];

  // const handleKeyDown = event => {
  //   console.log(event.key);
  //   // if (event.key.toLowerCase() == "esc") {
  //   //   close();
  //   // }
  // };

  // useEffect(() => {
  //   document.addEventListener("keydown", handleKeyDown);

  //   return () => {
  //     document.removeEventListener("keydown", handleKeyDown);
  //   };
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, []);

  const handleSubmit = e => {
    e.preventDefault();
    const privateForm = {
      email: email,
      type: access
    };
    setLoading(true);
    setError(null);
    shareDoc(id, type === "private" ? privateForm : { type: access })
      .then(res => {
        toast.success("Share successful");
        mutate(`/v1.1/docs/${id}/share`);
        setViewShares(true);
        setError(null);
      })
      .catch(err => {
        if (
          err?.response?.data?.code == "already_has_permission" ||
          err?.response?.data?.code == "already_has_folder_permission"
        ) {
          setError("User already has access");
        }

        if (err?.response?.data?.code == "user_not_found") {
          setError("User with the given email not found");
        }
      })
      .finally(() => {
        setLoading(false);
      });
  };

  return (
    <Dialog open={state} className="px-5" aria-labelledby="Share">
      <div className={`py-4 w-64 lg:w-96 bg-white`}>
        {!viewShares && (
          <form onSubmit={handleSubmit}>
            <div className="px-3 flex justify-between items-center mb-3">
              <div />
              <h4 className="text-center m-0 text-lg">Share</h4>

              <AiOutlineClose
                className="text-xl cursor-pointer"
                onClick={close}
              />
            </div>

            <hr />
            <div className="px-5 mt-1">
              <Select
                options={options}
                className="mb-3"
                onChange={e => setType(e.value)}
                placeholder="Private"
                defaultValue="private"
              />

              <p className="m-0 mb-1 font-medium">Access Level</p>
              <Select
                options={accessOptions}
                className="mb-3"
                onChange={e => setAccess(e.value)}
                defaultValue="read"
                placeholder="Read"
              />

              {type === "public" && (
                <CopyToClipboard
                  text={`${window.origin}/?docid=${id}`}
                  onCopy={() => setCopied(true)}
                >
                  <div className="copy-grid gap-3 items-center w-full">
                    <div className="-mt-4">
                      <InputField
                        required={true}
                        value={`${window.origin}/?docid=${id}`}
                        readOnly
                      />
                      {/* <span className="-mb-2">Copied</span> */}
                    </div>

                    <span className="text-white font-medium text-center text-sm block py-1 bg-blue-500 cursor-pointer outline-none border-none border-0 ">
                      Copy
                    </span>
                  </div>
                </CopyToClipboard>
              )}
              {copied && <span className="-mt-3 block italic">Copied</span>}

              {type === "private" && (
                <>
                  <InputField
                    label="Email"
                    required={true}
                    type="email"
                    placeholder="Email"
                    handleChange={e => setEmail(e.target.value)}
                    value={email}
                  />
                  {error && !loading && (
                    <span className="-mt-3 block italic text-red-500 text-sm">
                      {error}
                    </span>
                  )}
                </>
              )}
              <div className="mt-3">
                {loading ? (
                  <MiniLoader />
                ) : (
                  <div className="flex items-center justify-between">
                    <p
                      className="m-0 cursor-pointer italic flex items-center space-x-1 "
                      onClick={() => setViewShares(true)}
                    >
                      <HiInformationCircle className="m-0 text-lg" />
                      <span>View Shares</span>
                    </p>

                    <button
                      type="submit"
                      className="text-white font-medium lg:text-lg px-3 py-1 bg-lkhRed outline-none border-none border-0 "
                    >
                      Share
                    </button>
                  </div>
                )}
              </div>
            </div>
          </form>
        )}
        {viewShares && (
          <div>
            <div className="px-5 flex justify-between items-center mb-3">
              <BiArrowBack
                className="text-xl cursor-pointer"
                onClick={() => {
                  setViewShares(false);
                }}
              />

              <h4 className="text-center m-0 text-lg">Shares</h4>

              <AiOutlineClose
                className="text-xl cursor-pointer"
                onClick={close}
              />
            </div>
            <hr />

            {fetch && <MiniLoader />}

            {!data?.shares?.length && !fetch && (
              <p className="text-center">No shares yet</p>
            )}

            {!fetch &&
              data?.shares.map((m, i) => (
                <div className="share-grid mb-3 px-5">
                  <div className="flex items-center space-x-2">
                    <IoMdMail />
                    <p>{m?.email || "Public"}</p>
                  </div>

                  <p className="m-0">{m?.type}</p>

                  <AiOutlineClose
                    className="text-base cursor-pointer ml-auto"
                    onClick={() => {
                      deleteShare(id, m?.email)
                        .then(res => {
                          toast.success("Access removed ");
                          mutate(`/v1.1/docs/${id}/share`);
                        })
                        .catch(err => toast.error(err?.response?.data?.code));
                    }}
                  />
                </div>
              ))}
          </div>
        )}
      </div>
    </Dialog>
  );
};

export default ShareModal;
