import { Dialog } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import { AiOutlineClose } from "react-icons/ai";
import { toast } from "react-toastify";
import InputField from "../../components/InputField/InputField";
import { RenameDoc } from "../../utils/ApiRequests";
import docsManager from "../../utils/DocsManager";
import MiniLoader from "../Loaders/MiniLoader";

const RenameModal = ({ state, close, m }) => {
  const [newName, setName] = useState(m?.name);
  const [loading, setLoading] = useState(false);
  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);
    RenameDoc(m?.docid, {
      name: newName,
      parent: m?.parent
    })
      .then(res => {
        toast.success("Renamed Successfully");
        docsManager.reload();
      })
      .catch(err => toast.error(err?.response?.data?.code))
      .finally(() => {
        setLoading(false);
        close();
        // docsManager.relo
      });
  };

  // const handleKeyDown = event => {
  //   if (event.key.toLowerCase()) {
  //     close();
  //   }
  // };

  // useEffect(() => {
  //   document.addEventListener("keydown", handleKeyDown);

  //   return () => {
  //     document.removeEventListener("keydown", handleKeyDown);
  //   };
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, []);
  return (
    <Dialog open={state} className="px-5">
      <form
        action=""
        className="py-2 w-64 lg:w-96 bg-white "
        onSubmit={handleSubmit}
      >
        <div className="flex justify-between items-center my-2 px-2">
          <div />
          <h4 className="text-center m-0">Rename</h4>

          <AiOutlineClose className="text-xl cursor-pointer" onClick={close} />
        </div>

        <hr className="-mb-5" />

        <div className="px-2">
          <InputField
            // label="Rename"
            placeholder="Name"
            handleChange={e => setName(e.target.value)}
            value={newName}
          />

          <div className="text-right">
            {loading ? (
              <MiniLoader />
            ) : (
              <button
                type="submit"
                className="text-white font-medium lg:text-lg px-3 py-1 bg-lkhRed outline-none border-none border-0 mx-auto"
              >
                Rename
              </button>
            )}
          </div>
        </div>
      </form>
    </Dialog>
  );
};

export default RenameModal;
