import { AiOutlineClose } from "react-icons/ai";
import { Dialog } from "@material-ui/core";
import Select from "react-select";
import { useEffect, useState } from "react";
import MiniLoader from "../Loaders/MiniLoader";
import { toast } from "react-toastify";
import docsManager from "../../utils/DocsManager";
import userInfo from "../../utils/UserInfo";
import { MoveDoc } from "../../utils/ApiRequests";
import { AiFillFolder, AiOutlineRight } from "react-icons/ai";
import { BiArrowBack } from "react-icons/bi";
import { IoArrowBackCircleSharp } from "react-icons/io5";

const MoveModal = ({ state, close, m }) => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState();
  const [parent, setParent] = useState(userInfo?.data?.userid);
  const [id, setId] = useState(null);
  // const handleKeyDown = event => {
  //   if (event.key.toLowerCase()) {
  //     close();
  //   }
  // };

  // useEffect(() => {
  //   document.addEventListener("keydown", handleKeyDown);

  //   return () => {
  //     document.removeEventListener("keydown", handleKeyDown);
  //   };
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, []);
  // // console.log(data);

  // // console.log(m.docid);

  useEffect(() => {
    setLoading(true);
    docsManager
      .getChildFolders(parent || userInfo?.data?.userid)
      .then(res => {
        setData(res);
        setLoading(false);
      })
      .catch(err => console.log(err.response));
  }, [parent]);

  // console.log(options);
  const handleSubmit = e => {
    e.preventDefault();
    setLoading(true);
    MoveDoc(m?.docid, {
      parent: parent || userInfo?.data?.userid,
      name: m?.name
    })
      .then(res => {
        docsManager.reset();
        docsManager.reload();
        close();
      })
      .catch(err => toast.error(err?.response?.data?.code))
      .finally(() => {
        setLoading(false);
      });
  };

  return (
    <Dialog open={state} className="px-5" aria-labelledby="Share">
      <form
        className={`py-2 h-72 w-64 lg:w-96 bg-white`}
        onSubmit={handleSubmit}
        // onClick
      >
        <div className="flex items-center justify-between my-2 px-3">
          {parent != userInfo?.data?.userid && (
            <BiArrowBack
              className="text-xl cursor-pointer"
              onClick={() => {
                setId(userInfo?.data?.userid);
                setParent(userInfo?.data?.userid);
              }}
            />
          )}

          <h4 className="text-center m-0">Move Document</h4>

          <AiOutlineClose
            className="text-xl cursor-pointer"
            onClick={() => close()}
          />
        </div>
        <hr />

        {loading && (
          <div className="mt-6">
            <MiniLoader />
          </div>
        )}
        {!loading && (
          <>
            <div className="h-1/2 space-y-6 py-2 overflow-y-auto px-3">
              {data?.map((m, i) => (
                <div
                  className={`move-grid text-lg ${
                    id === m.docid ? "bg-gray-200" : ""
                  }`}
                  key={i}
                >
                  <div
                    className="flex items-center space-x-2 cursor-pointer"
                    onClick={() => setId(m.docid)}
                  >
                    <AiFillFolder />
                    <span>{m.name}</span>
                  </div>
                  <AiOutlineRight
                    className="cursor-pointer text-right ml-auto"
                    onClick={() => setParent(m.docid)}
                  />
                </div>
              ))}
            </div>

            <div className="text-right mt-8 px-3">
              <button
                type="submit"
                className="text-white font-medium lg:text-lg px-3 py-1 bg-lkhRed outline-none border-none border-0 mx-auto"
              >
                Move Here
              </button>
            </div>
          </>
        )}
      </form>
    </Dialog>
  );
};

export default MoveModal;
